
import React, { useState } from "react";
import AliceCarousel from 'react-alice-carousel';

import { Left } from '../../components/Left';
// import { Blockbear } from '../../components/Blockbear';

import { getImg } from "../../utils/Helper";

import "react-alice-carousel/lib/alice-carousel.css";
import Styles from './Staking.module.scss';

export const Staking = () => {

  return (
    <div className={Styles.home}>
      <Left color3="white" />
      <div className={Styles.contain}>
        <p className={Styles.top_title}>
          STAKING
        </p>
        <p className={Styles.top_context}>
          Lorem ipsum dolor sit amet, consectetur adipiscing eli.
        </p>
        <div className={Styles.top}>
          <div className={Styles.top_contain}>
            <div className={Styles.top_contain_left}>
              <img src={getImg('home/cherry.png')} />
              <div className={Styles.total}>
                <p className={Styles.first}>+5 bearries/DAY</p>
                <p className={Styles.second}>Daily bearries</p>
              </div>
            </div>
            <div className={Styles.top_contain_left}>
              <img src={getImg('home/yogi.png')} />
              <div className={Styles.total}>
                <p className={Styles.first}>4 BEARS STAKED</p>
                <p className={Styles.second}>TOTAL STAKED</p>
              </div>
            </div>
            <div className={Styles.top_contain_left}>
              <img src={getImg('home/cherry.png')} />
              <div className={Styles.total}>
                <p className={Styles.first}> 456 $bearries</p>
                <p className={Styles.second}>TOTAL EARNED</p>
              </div>
            </div>
          </div>
        </div>
        <div className={Styles.main}>
          <div className={Styles.main_left}>
            <div className={Styles.main_left_contain}>
              <center>
                <p className={Styles.title}>StakE your bears</p>
                <div className={Styles.secret}>
                  <div className={Styles.ids}>
                    <p>BEARS IDs</p>
                    <p style={{ color: '#6B83FF' }}>STAKE ALL</p>
                  </div>
                  <div className={Styles.comma}>
                    {/* <p>2345, 2494, 9303</p>
                    <p>SEPARATE ID BY COMMAS</p> */}
                    <input defaultValue='2345 2494 9303' />
                    <p>SEPARATE ID BY COMMAS</p>
                  </div>
                </div>
                <p><span style={{ color: '#777677' }}>QUANTITY:</span> 3 BEARS</p>
                <p><span style={{ color: '#777677' }}>Bonus earned:</span> + 456 $bearries / DAY</p>
                <img src={getImg('home/stakingbtn0.png')} />
              </center>
            </div>
          </div>
          <div className={Styles.main_right}>
            <div className={Styles.main_right_contain}>
              <center>
                <p className={Styles.title}>UNSTACK and CLAIM</p>
                <div className={Styles.secret}>
                  <div className={Styles.ids}>
                    <p>BEARS IDS</p>
                    <p style={{ color: '#6B83FF' }}>UNSTAKE ALL</p>
                  </div>
                  <div className={Styles.comma}>
                    <input defaultValue='2345 2494 9303' />
                    <p style={{ color: 'transparent' }}>SEPARATE ID BY COMMAS</p>
                  </div>
                </div>
                <img src={getImg('home/stakingbtn2.png')} />
                <div className={Styles.secret}>
                  <div className={Styles.ids}>
                    <p>AMOUNT TO WITHDRAW</p>
                    <p style={{ color: '#6B83FF' }}>WITHDRAW ALL</p>
                  </div>
                  <div className={Styles.comma}>
                    <input defaultValue='50' />
                    <p style={{ color: 'transparent' }}>SEPARATE ID BY COMMAS</p>
                  </div>
                </div>
                <img src={getImg('home/stakingbtn1.png')} />
              </center>
            </div>
          </div>
        </div>
      </div >
    </div >
  )
}
