import React, { useState } from "react";

import { getImg } from "../../utils/Helper";

import Styles from './Blockshop.module.scss';

export const Blockshop = () => {

  const myItems = [
    { image1: 'shop1.png', image2: 'cherry.png', title: 'IMX BEARS TEE-SHIRT', content1: '805 $BEARRIS', button: 'purchase.png' },
    { image1: 'shop2.png', image2: 'cherry.png', title: 'IMX BEARS TEE-SHIRT', content1: '805 $BEARRIS', button: 'purchase1.png' },
    { image1: 'shop1.png', image2: 'cherry.png', title: 'IMX BEARS TEE-SHIRT', content1: '805 $BEARRIS', button: 'purchase.png' },
    { image1: 'shop1.png', image2: 'cherry.png', title: 'IMX BEARS TEE-SHIRT', content1: '805 $BEARRIS', button: 'purchase.png' },
    { image1: 'shop1.png', image2: 'cherry.png', title: 'IMX BEARS TEE-SHIRT', content1: '805 $BEARRIS', button: 'purchase.png' },
    { image1: 'shop1.png', image2: 'cherry.png', title: 'IMX BEARS TEE-SHIRT', content1: '805 $BEARRIS', button: 'purchase.png' },
  ];

  return (
    <div className={Styles.main}>
      {
        myItems.map((item) => (
          <>
            <div className={Styles.main_contain}>

              <div className={Styles.block}>
                <img src={getImg('home/' + item.image1)} />
              </div>
              <center>
                <p>{item.title}</p>
                <div className={Styles.content1} style={{ display: 'flex' }}>
                  <img src={getImg('home/' + item.image2)} style={{ width: '20px' }} />
                  <p style={{ fontSize: '12px', alignSelf: 'center', color: '#777677' }}>{item.content1} </p>
                </div>
                {/* <p>{item.content2}</p> */}
                <img src={getImg('home/' + item.button)} className={Styles.btn} />
              </center>
            </div>

          </>
        ))
      }
    </div>

  )
}
