import React, { useState } from "react";

import { getImg } from "../../utils/Helper";

import Styles from './Blockbear.module.scss';

export const Blockbear = () => {

  const myItems = [
    { image: 'pink man.png', title: 'BEAR #2394', content1: 'RANK 2021', content2: 'VIEW ON OPEANSEA >' },
    { image: 'pink man.png', title: 'BEAR #2394', content1: 'RANK 2021', content2: 'VIEW ON OPEANSEA >' },
    { image: 'pink man.png', title: 'BEAR #2394', content1: 'RANK 2021', content2: 'VIEW ON OPEANSEA >' },
    { image: 'pink man.png', title: 'BEAR #2394', content1: 'RANK 2021', content2: 'VIEW ON OPEANSEA >' },
    { image: 'pink man.png', title: 'BEAR #2394', content1: 'RANK 2021', content2: 'VIEW ON OPEANSEA >' },
    { image: 'pink man.png', title: 'BEAR #2394', content1: 'RANK 2021', content2: 'VIEW ON OPEANSEA >' },
  ];

  return (
    <div className={Styles.main}>
      {
        myItems.map((item) => (
          <>
            <div className={Styles.main_contain}>

              <div className={Styles.block}>
                <img src={getImg('home/' + item.image)} />
              </div>
              <center>
                <p>{item.title}</p>
                <p style={{ fontSize: '12px', color: '#777677' }}>{item.content1} </p>
                <p>{item.content2}</p>
              </center>
            </div>

          </>
        ))
      }
    </div>

  )
}
