import React, { useState } from "react";

import { getImg } from "../../utils/Helper";
import Styles from './Left.module.scss';
import { Link } from 'react-router-dom';
import Hamburger from 'hamburger-react';

import styled from 'styled-components';

const Leftbar = styled.div`
    .hamburger-react {
    @apply top-8 right-4 rounded z-50;
    position: fixed !important;
    background: rgba(153, 153, 153, 0.32);
    right : 1rem;
    top : 1rem;
    @media (min-width:1301px) {
      display: none;
    }
  }
`;



export const Left = (props) => {

  const [isOpen, setOpen] = useState(false);

  const Navigation = (props) => (
    <div className={Styles.menu}>
      <div className={Styles.mcontain}>
        <p className={Styles.mtitle} style={{ color: 'white' }}>IMX BEARS</p>
        <div className={Styles.mlist}>
          <Link to='/home'><a style={{ color: props.color1 }}>HOME</a></Link>
          <Link to='/mybears'><a style={{ color: props.color2 }}>MY BEARS</a></Link>
          <Link to='/staking'><a style={{ color: props.color3 }}>STAKING</a></Link>
          <Link to='/shop'><a style={{ color: props.color4 }}>SHOP</a></Link>
        </div>
        <center>
          <div style={{ marginTop: '100px' }}>
            <img src={getImg('home/pink man.png')} className={Styles.pinkman} />
            <img src={getImg('home/leftbutton.jpg')} className={Styles.leftbutton} style={{ width: '145px' }} />

          </div>
        </center>
      </div>
    </div>
  );

  return (
    <>
      <Leftbar>
        <div className={Styles.left}>
          <div className={Styles.contain}>
            <p className={Styles.title}>IMX BEARS</p>
            <div className={Styles.list}>
              <Link to='/home'><a style={{ color: props.color1 }}>HOME</a></Link>
              <Link to='/mybears'><a style={{ color: props.color2 }}>MY BEARS</a></Link>
              <Link to='/staking'><a style={{ color: props.color3 }}>STAKING</a></Link>
              <Link to='/shop'><a style={{ color: props.color4 }}>SHOP</a></Link>
            </div>
            <center>
              <div style={{ marginTop: '100px' }}>
                <img src={getImg('home/pink man.png')} className={Styles.pinkman} />
                <img src={getImg('home/leftbutton.jpg')} className={Styles.leftbutton} />

              </div>
              {/* <p>
            0x354...393939 >
          </p> */}
              {/* <button>0x354...393939></button> */}
            </center>
          </div>
        </div>

        <Hamburger toggled={isOpen} toggle={setOpen} color='#fff' />
        {isOpen && <Navigation />}

      </Leftbar>
    </>
  )
}